<template>
  <div class="modal">
    <div class="back" @click="closeModalAnimation()">
      <i class="fas fa-chevron-left"></i>
    </div>
    <img :src="modal.info.image" alt="recipeImage" />
    <div :class="[{ active: activeScroll }, 'info']" @scroll="onScrolled()">
      <div class="main">
        <h3>{{ modal.info.label }}</h3>

        <div class="calories">
          {{ Math.round(modal.info.calories / modal.info.yield) }}
          <br />calories
        </div>
        <div class="stars">
          <div class="rate">
            <div>
              <i class="fas fa-star checked"></i>
            </div>
            <div :class="{ checked: modal.info.yield > 3 }">
              <i class="fas fa-star"></i>
            </div>
            <div :class="{ checked: modal.info.yield > 5 }">
              <i class="fas fa-star"></i>
            </div>
            <div :class="{ checked: modal.info.yield > 7 }">
              <i class="fas fa-star"></i>
            </div>
            <div :class="{ checked: modal.info.yield > 9 }">
              <i class="fas fa-star"></i>
            </div>
          </div>
          <span>{{ modal.info.yield }}k. rates</span>
          <button @click="$emit('showRateModal')">Rate</button>
        </div>
      </div>
      <div class="additional">
        <div>
          <span>Servings:</span>
          <br />
          <span>{{ modal.info.yield }}pp</span>
        </div>
        <div>
          <span>Weight:</span>
          <br />
          <span>{{ Math.round(modal.info.totalWeight / modal.info.yield) }}g</span>
        </div>
        <div>
          <span>Daily:</span>
          <br />
          <span>
            {{
            Math.round((modal.info.calories / modal.info.yield / 2250) * 100)
            }}%
          </span>
          <br />
        </div>
      </div>
      <div class="labels">
        <div
          :class="[{ activeLabel: isIngredientsActive }, 'label']"
          @click="changeActiveIngredient('ingredients')"
        >Ingredients</div>
        <div
          :class="[{ activeLabel: !isIngredientsActive }, 'label']"
          @click="changeActiveIngredient('nutrients')"
        >Nutrients</div>
      </div>

      <div class="ingredients" v-if="isIngredientsActive">
        <div
          class="ingredient"
          v-for="ingredient in modal.info.ingredients"
          :key="ingredient.text"
        >{{ ingredient.text }}</div>
      </div>
      <div class="nutrients" v-else>
        <div v-for="nutrient in modal.info.totalNutrients" :key="nutrient.label" class="nutrient">
          <span>{{ nutrient.label }}</span>
          <span>{{ Math.round(nutrient.quantity) + " " + nutrient.unit }}</span>
        </div>
      </div>
      <a :href="modal.info.url">
        <button class="full">Get full recipe instructions</button>
      </a>
    </div>
  </div>
</template>

<script>
import gsap from "gsap";
export default {
  props: {
    modal: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      activeScroll: false,
      isIngredientsActive: true
    };
  },
  methods: {
    onScrolled() {
      if (this.windowWidth < 1024) {
        this.activeScroll = true;
      }
    },

    closeModalAnimation() {
      this.activeScroll = false;
      gsap.to(".modal .back", {
        display: "none"
      });

      gsap.to(".modal img", {
        y: 100 + "vh",
        x: -400,
        duration: 0.5,
        ease: "ease-out"
      });

      gsap.to(".modal .info", {
        y: 100 + "vh",
        x: 300,
        duration: 0.5,
        ease: "ease-in-out"
      });
    },
    changeActiveIngredient(element) {
      if (element == "ingredients" && !this.isIngredientsActive) {
        this.isIngredientsActive = true;
      } else if (element == "nutrients" && this.isIngredientsActive) {
        this.isIngredientsActive = false;
      }
    }
  }
};
</script>

<style lang="scss" scoped>
$main-color: #c92e43;

.modal {
  .back {
    position: fixed;
    top: 20px;
    left: 20px;
    color: $main-color;
    font-size: 2.3em;
    z-index: 101;
    display: none;
    cursor: pointer;
  }
  img {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    height: 50vh;
    z-index: 100;
    border-radius: 0 0 30px 30px;
    display: none;
  }

  .info.active {
    height: 83vh;
    transition: 0.5s;
  }

  .info {
    width: 100%;
    height: 54vh;
    position: fixed;
    bottom: 0;
    background: #fff;
    border-radius: 50px 50px 0 0;
    z-index: 101;
    display: none;
    overflow-y: scroll;

    .main {
      h3 {
        color: $main-color;
        margin-bottom: 3px;
        margin-top: 25px;
        font-size: 1.4em;
        padding-left: 30px;
        width: 75vw;
      }

      .calories {
        position: absolute;
        top: 30px;
        right: 40px;
        text-align: right;
        color: #778079;
      }
      .stars {
        color: lightgrey;
        font-size: 1em;
        margin-top: 30px;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        padding: 20px 30px;

        .rate {
          display: flex;
        }
        span {
          font-size: 0.8em;
          align-self: center;
        }
        button {
          border-radius: 20px;
          border: 0 none;
          padding: 5px 10px;
          font-weight: bold;
          font-size: 0.7em;
          background-color: lightgrey;
          cursor: pointer;
        }
        .checked {
          color: $main-color;
        }
      }
    }

    .additional {
      width: 100%;
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      padding: 10px 20px;
      margin-top: 20px;
      div {
        justify-self: center;
      }
      div span {
        font-size: 1.3em;
        font-weight: bold;
      }
      div span:first-child {
        color: #778079;
        font-size: 0.9em;
        font-weight: normal;
      }
    }
  }

  .labels {
    display: flex;
    margin-top: 50px;
    .label {
      padding: 15px 30px;
      width: 50%;
      background-color: #e8eaed;
      color: gray;
      text-align: center;
      cursor: pointer;
    }

    .activeLabel {
      border-bottom: 3px solid $main-color;
      color: $main-color;
      cursor: default;
    }
  }
  .ingredients {
    .ingredient {
      border-bottom: 2px solid lightgray;
      padding: 15px 25px;
    }
  }

  .nutrients {
    .nutrient {
      width: 100%;

      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-gap: 100px;
      padding: 7px 20px;
      font-size: 0.8em;
    }

    .nutrient:nth-child(even) {
      background-color: #f2f3f5;
    }
  }
  a {
    width: 100%;
    display: flex;
    justify-content: center;
    text-decoration: none;
    .full {
      padding: 10px 40px;
      background-color: $main-color;
      border: 3px solid $main-color;
      border-radius: 10px;
      margin: 50px auto;
      color: #fff;
      font-size: 0.9em;
      font-family: "Montserrat", sans-serif;
      cursor: pointer;

      &:hover {
        color: #000;
        background: #fff;
        transition: 0.3s;
      }
    }
  }
}

@media screen and (min-width: 1024px) {
  .modal {
    img {
      height: 100vh;
      width: 50%;
      border-radius: 0;
    }

    .info {
      width: 50%;
      height: 100vh;
      overflow-x: hidden;
      right: 0;
      border-radius: 0;
      /* width */
      &::-webkit-scrollbar {
        width: 10px;
      }

      /* Track */
      &::-webkit-scrollbar-track {
        background: #f1f1f1;
      }

      /* Handle */
      &::-webkit-scrollbar-thumb {
        background: $main-color;
        border-radius: 20px;
      }

      /* Handle on hover */
      &::-webkit-scrollbar-thumb:hover {
        background: #f53d6b;
      }
      .main {
        h3 {
          font-size: 2em;
          max-width: 70%;
        }

        .stars {
          font-size: 1.2em;
          margin-top: 10px;

          button {
            width: 200px;
            padding: 10px;
          }
        }
      }

      .additional {
        font-size: 1.2em;
      }
    }
  }
}

@media screen and (min-width: 1324px) {
  .modal .info .main .stars {
    font-size: 1.5em;
  }
}
</style>
