<template>
  <div class="most-liked">
    <h3>Most Liked Recipes</h3>
    <div class="liked-recipes">
      <div class="left-arrow arrow" @click="scrollLeft()">
        <i class="fas fa-chevron-left"></i>
      </div>
      <div class="right-arrow arrow" @click="scrollRight()">
        <i class="fas fa-chevron-right"></i>
      </div>
      <div class="recipes">
        <div
          class="like-recipe"
          v-for="recipe in recipes
            .slice(1, 35)
            .filter(item => item.recipe.yield >= 5)
            .sort((a, b) => b.recipe.yield - a.recipe.yield)"
          :key="recipe.recipe.calories"
          @click="$emit('showModal', recipe.recipe.image)"
        >
          <img :src="recipe.recipe.image" alt="foodImage" />
          <span>{{ recipe.recipe.label }}</span>
          <div class="stars">
            <i class="fas fa-star checked"></i>
            <i class="fas fa-star checked"></i>
            <i class="fas fa-star checked"></i>
            <i
              :class="[
                recipe.recipe.yield >= 7 ? 'checked' : '',
                'fas',
                'fa-star'
              ]"
            ></i>
            <i
              :class="[
                recipe.recipe.yield >= 9 ? 'checked' : '',
                'fas',
                'fa-star'
              ]"
            ></i>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    recipes: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      scrolledValue: 0
    };
  },
  methods: {
    scrollLeft() {
      const element = document.querySelector(".recipes");
      this.scrolledValue -= (this.windowWidth / 220).toFixed(0) * 220;
      element.style.transition = "1s";
      if (this.scrolledValue > 0) {
        document.querySelector(".right-arrow").style.display = "block";
        element.style.transform = "translateX(" + -this.scrolledValue + "px)";
      } else if (this.scrolledValue <= 0) {
        this.scrolledValue = 0;
        element.style.transform = "translateX(" + -this.scrolledValue + "px)";
        document.querySelector(".left-arrow").style.display = "none";
      }
    },
    scrollRight() {
      const element = document.querySelector(".recipes");
      let maxScroll =
        this.recipes.slice(1, 35).filter(item => item.recipe.yield >= 5)
          .length *
          220 -
        this.windowWidth;
      this.scrolledValue += (this.windowWidth / 220).toFixed(0) * 220;
      element.style.transition = "1s";
      document.querySelector(".left-arrow").style.display = "block";
      if (maxScroll < this.scrolledValue) {
        element.style.transform = "translateX(" + -(maxScroll + 20) + "px)";
        this.scrolledValue = maxScroll;
        document.querySelector(".right-arrow").style.display = "none";
      } else {
        element.style.transform = "translateX(" + -this.scrolledValue + "px)";
      }
    }
  }
};
</script>

<style lang="scss" scoped>
$main-color: #c92e43;
.most-liked {
  h3 {
    padding-left: 20px;
    margin-top: 30px;
    font-size: 0.8em;
  }
  .liked-recipes {
    overflow-x: scroll;

    scrollbar-width: none;
    position: relative;

    .recipes {
      display: flex;
      padding: 0 10px;
    }
    &::-webkit-scrollbar {
      display: none; /* Chrome Safari */
    }

    .like-recipe:hover {
      -webkit-box-shadow: 3px 18px 120px -39px rgba(0, 0, 0, 0.75);
      -moz-box-shadow: 3px 18px 120px -39px rgba(0, 0, 0, 0.75);
      box-shadow: 3px 18px 120px -39px rgba(0, 0, 0, 0.75);
      transition: 0.3s;
      transform: translateY(-2px);
      border-radius: 5px;
      cursor: pointer;
    }
    .arrow {
      position: absolute;
      color: #fff;
      padding: 10px 20px;
      border-radius: 50%;
      background: $main-color;
      font-size: 2em;
      top: 30%;
      z-index: 90;
      cursor: pointer;
    }

    .left-arrow {
      left: 20px;
      display: none;
      animation: left 0.7s linear infinite;
    }

    .right-arrow {
      right: 20px;
      animation: right 0.7s linear infinite;
    }
    img {
      width: 200px;
      height: 160px;
      border-radius: 10px;
      margin: 5px 10px;
    }

    span {
      padding-left: 10px;
      font-size: 0.9em;
      width: 100%;
      display: block;
    }

    .stars {
      padding-left: 10px;
      color: lightgrey;
      font-size: 0.8em;
      margin-top: 5px;

      .checked {
        color: $main-color;
      }
    }
  }
}

@media (hover: none) and (pointer: coarse) {
  .arrow {
    display: none;
  }
}

@keyframes right {
  0% {
    transform: translateX(0px);
  }
  50% {
    transform: translateX(-10px);
  }
  100% {
    transform: translateX(0px);
  }
}

@keyframes left {
  0% {
    transform: translateX(0px);
  }
  50% {
    transform: translateX(10px);
  }
  100% {
    transform: translateX(0px);
  }
}
</style>
