<template>
  <div v-if="!loading">
    <Start v-if="activeComponent == 'Start'" @change="changeActiveComponent" />
    <div v-if="activeComponent == 'Search'">
      <Search @searchFood="getRecipes" />
      <div v-if="recipesData.length">
        <likedRecipes :recipes="recipesData" @showModal="showModal" />
        <popularRecipes :recipes="recipesData" @showModal="showModal" />
        <Modal :modal="modal" @showRateModal="isRateModalShowed = true" />
        <transition name="show">
          <rateModal v-if="isRateModalShowed" @closeRateModal="isRateModalShowed = false" />
        </transition>
      </div>
      <div class="error" v-else>
        <span>Unfortunately, we don't have what you're looking for.</span>
        <div class="icon">
          <i class="far fa-frown"></i>
        </div>
      </div>
    </div>
  </div>
  <div class="loader" v-else>
    <div class="sk-folding-cube">
      <div class="sk-cube1 sk-cube"></div>
      <div class="sk-cube2 sk-cube"></div>
      <div class="sk-cube4 sk-cube"></div>
      <div class="sk-cube3 sk-cube"></div>
    </div>
  </div>
</template>
<script>
import Start from "./components/start.vue";
import Search from "./components/search.vue";
import likedRecipes from "./components/likedRecipes.vue";
import popularRecipes from "./components/popularRecipes.vue";
import Modal from "./components/modal.vue";
import rateModal from "./components/rateModal.vue";

import axios from "axios";
import gsap from "gsap";
export default {
  components: {
    Start,
    Search,
    likedRecipes,
    popularRecipes,
    Modal,
    rateModal
  },
  data() {
    return {
      activeComponent: "Start",
      recipesData: [],
      loading: true,
      modal: {
        backActive: false,
        info: {}
      },
      isRateModalShowed: false
    };
  },

  methods: {
    getRecipes(searchValue) {
      this.loading = true;
      if (searchValue == "") {
        searchValue = "chicken";
      }
      return axios
        .get(
          `https://api.edamam.com/search?q=${searchValue}&app_id=acd699fd&app_key=11d86b2496ade8b9612d286c3cf71403&to=100`
        )
        .then(response => {
          this.recipesData = response.data.hits;
        })
        .catch(err => alert(err))
        .finally(() => (this.loading = false));
    },
    changeActiveComponent(componentName) {
      this.activeComponent = componentName;
    },
    showModal(image) {
      this.modal.info = this.recipesData.filter(
        item => item.recipe.image == image
      )[0].recipe;
      console.log(this.modal.info.yield);
      gsap.to(".modal .back", {
        display: "block"
      });
      gsap.fromTo(
        ".modal img",
        {
          y: 100 + "vh",
          x: -200,
          duration: 0.5,
          ease: "ease-out",
          display: "block"
        },
        {
          display: "block",
          y: 0,
          x: 0
        }
      );

      gsap.fromTo(
        ".modal .info",
        {
          y: 100 + "vh",
          x: 300,
          duration: 0.5,
          ease: "ease-in-out",
          display: "block"
        },
        {
          display: "block",
          y: 0,
          x: 0
        }
      );
    }
  },
  created() {
    this.getRecipes("chicken");
  }
};
</script>
