<template>
  <div>
    <div class="rate">
      <div class="main">
        <div class="cross" @click="$emit('closeRateModal')">
          <i class="fas fa-times"></i>
        </div>
        <div v-if="!showThanks">
          <h2>Rate this recipe</h2>

          <div class="stars">
            <div :class="{ checked: checkedStars >= 1 }" @mouseover="checkStars(1)">
              <i class="fas fa-star"></i>
            </div>
            <div :class="{ checked: checkedStars >= 2 }" @mouseover="checkStars(2)">
              <i class="fas fa-star"></i>
            </div>
            <div :class="{ checked: checkedStars >= 3 }" @mouseover="checkStars(3)">
              <i class="fas fa-star"></i>
            </div>
            <div :class="{ checked: checkedStars >= 4 }" @mouseover="checkStars(4)">
              <i class="fas fa-star"></i>
            </div>
            <div :class="{ checked: checkedStars >= 5 }" @mouseover="checkStars(5)">
              <i class="fas fa-star"></i>
            </div>
          </div>
          <div class="button">
            <button @click="showThanks = true">Rate</button>
          </div>
        </div>
        <div class="thanks" v-else>
          <h3>Thanks for your opinion :)</h3>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      checkedStars: 0,
      showThanks: false
    };
  },
  methods: {
    checkStars(value) {
      this.checkedStars = value;
    }
  }
};
</script>

<style lang="scss" scoped>
$main-color: #c92e43;
.rate {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 110;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.3);
  display: flex;
  justify-content: center;
  align-items: center;

  .main {
    width: 80%;
    height: 33%;
    background: white;
    border-radius: 20px;
    position: relative;
    transform-origin: 100% 0%;
    animation: animate 0.5s ease-in-out;

    h2 {
      text-align: center;
      margin-top: 50px;
    }

    .cross {
      position: absolute;
      top: 10px;
      right: 20px;
      font-size: 1.5em;
      color: $main-color;
      cursor: pointer;
    }

    .stars {
      font-size: 2.3em;
      margin-top: 25px;
      color: lightgray;
      display: flex;
      justify-content: center;

      .checked {
        color: $main-color;
        animation: bounce-in 0.3s ease-in;
      }
    }
    .button {
      position: absolute;
      bottom: -30px;
      width: 100%;
      text-align: center;

      button {
        padding: 10px 40px;
        background-color: $main-color;
        border-radius: 10px;
        margin: 50px auto;
        color: #fff;
        font-size: 0.9em;
        border: 0 none;
        font-family: "Montserrat", sans-serif;
        cursor: pointer;
      }
    }

    .thanks {
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 20px;

      h3 {
        font-size: 1.4em;
        text-align: center;
      }
    }
  }
}

@keyframes animate {
  from {
    transform: scaleX(0);
  }
  to {
    transform: scaleX(1);
  }
}

@keyframes bounce-in {
  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(1.5);
  }
  100% {
    transform: scale(1);
  }
}

@media all and (min-width: 1024px) {
  .rate .main {
    width: 400px;
  }
}
</style>
