        :key="recipe.recipe.calories"
        @click="$emit('showModal', recipe.recipe.image)"
      >
        <img :src="recipe.recipe.image" alt="foodImage" />
        <div class="information">
          <span class="label">{{ recipe.recipe.label }}</span>
          <br />
          <span class="health">{{ recipe.recipe.healthLabels[0] }}</span>
          <div class="stars">
            <i class="fas fa-star checked"></i>
            <i
              :class="[
                recipe.recipe.yield >= 3 ? 'checked' : '',
                'fas',
                'fa-star'
              ]"
            ></i>
            <i
              :class="[
                recipe.recipe.yield >= 5 ? 'checked' : '',
                'fas',
                'fa-star'
              ]"
            ></i>
            <i
              :class="[
                recipe.recipe.yield >= 7 ? 'checked' : '',
                'fas',
                'fa-star'
              ]"
            ></i>
            <i
              :class="[
                recipe.recipe.yield >= 9 ? 'checked' : '',
                'fas',
                'fa-star'
              ]"
            ></i>
          </div>
        </div>
        <div class="calories">
          {{ (recipe.recipe.calories / recipe.recipe.yield).toFixed(0) }}
