<template>
  <div>
    <h2>Search</h2>
    <div class="searchbox">
      <input
        type="text"
        placeholder="Search"
        v-model="foodSearch"
        @keyup.enter="
          $emit('searchFood', foodSearch);
          foodSearch = '';
        "
      />
      <div
        class="icon"
        @click="
          $emit('searchFood', foodSearch);
          foodSearch = '';
        "
      >
        <i class="fas fa-search"></i>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      foodSearch: ""
    };
  }
};
</script>

<style lang="scss" scoped>
$main-color: #c92e43;
h2 {
  color: $main-color;
  font-size: 2em;
  padding-left: 20px;
}

.searchbox {
  width: 85%;

  margin: 0 auto;
  position: relative;

  input {
    background: transparent;
    border: 0 none;
    width: 80%;
    border: 2px solid $main-color;
    border-radius: 8px;
    padding: 7px 20px;
    font-size: 1.1em;
    transition: width 0.3s;

    &:focus {
      outline: 0 none;
      width: 78%;
      border: 0 none;
      padding: 7px 0;
      border-bottom: 2px solid $main-color;
      border-radius: 0px;
      transform: translateX(10px);
      transition: 0.3s;
    }
  }
  .icon {
    position: absolute;
    right: 10px;
    top: 7px;
    font-size: 1.5em;
    cursor: pointer;
  }
}

@media all and (min-width: 1024px) {
  h2 {
    display: none;
  }
  .searchbox {
    width: 40%;
    margin-top: 50px;

    input {
      width: 90%;

      &:focus {
        width: 88%;
      }
    }
  }
}
</style>